.card {
    width: 21%;
    height: 50%;
    background-image: radial-gradient(circle 759px at -6.7% 50%, 315deg, #ffffff 0%, #d7e1ec 74%);
    padding: 2rem 1.5rem;
    /* background-color: #ffffff;
    background-image: linear-gradient(315deg, #ffffff 0%, #d7e1ec 74%); */
    transition: box-shadow .3s ease, transform .2s ease;
    position: absolute;
    display: block;
  
    left: 39%;
    margin-top: 10%;
  }
  
  .card2 {
  
    width: 21%;
    height: 50%;
    background-image: radial-gradient(circle 759px at -6.7% 50%, 315deg, #ffffff 0%, #d7e1ec 74%);
    padding: 2rem 1.5rem;
    /* background-color: #ffffff;
    background-image: linear-gradient(315deg, #ffffff 0%, #d7e1ec 74%); */
    transition: box-shadow .3s ease, transform .2s ease;
    position: absolute;
    display: block;
    border-radius: 2%;
    border-color: black;
    left: 8%;
    margin-top: 10%;
  
  }
  
  .card3 {
  
    width: 21%;
    height: 50%;
    background-image: radial-gradient(circle 759px at -6.7% 50%, 315deg, #ffffff 0%, #d7e1ec 74%);
    padding: 2rem 1.5rem;
    /* background-color: #ffffff;
    background-image: linear-gradient(315deg, #ffffff 0%, #d7e1ec 74%); */
    transition: box-shadow .3s ease, transform .2s ease;
    position: absolute;
    border-radius: 2%;
    display: block;
  
  
    left: 69%;
    margin-top: 10%;
  
  }
  
  .imgr1 {
    position: absolute;
    left: 1%;
    right: 2%;
  
    width: 25%;
  }
  .imgr2 {
    position: absolute;
    top: 50%;
    width: 95%;
  }
  .main {
    background: url("/Applications/XAMPP/xamppfiles/htdocs/wastewise/wastewise/src/pages/image/images-bg.jpg") repeat-y center center/cover fixed;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    overflow: auto;
  
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    z-index: -1;
  
  }
  
  .imgr {
    position: relative;
  }
  
  .card-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: transform .2s ease, opacity .2s ease;
  }
  
  .card-title{
    font-family:'Montserrat';
  }
  .card2-title{
    font-family:'Montserrat';
  }
  .card3-title{
    font-family:'Montserrat';
  }
  /*Image*/
  .card-avatar {
    --size: 60px;
    background: url('/Applications/XAMPP/xamppfiles/htdocs/wastewise/wastewise/src/pages/image/3A910693-A382-4E05-9514-4FC2DFA0ECC5_1_105_c.jpg');
    width: var(--size);
    height: var(--size);
    border-radius: 50%;
    transition: transform .2s ease;
    margin-bottom: 1rem;
  }
  
  
  /*Card footer*/
  .card-social {
    transform: translateY(200%);
    margin-top: 30%;
    display: flex;
    justify-content: space-around;
    width: 100%;
    opacity: 0;
    transition: transform .2s ease, opacity .2s ease;
  }
  
  .card-social__item {
    list-style: none;
    justify-content: center;
    width: 30%;
  }
  
  .card-social__item svg {
    display: block;
  
    justify-content: center;
    height: 18px;
    width: 18px;
  
    fill: #515F65;
    cursor: pointer;
    transition: fill 0.2s ease, transform 0.2s ease;
  }
  
  /*Text*/
  .card-title {
    color: #333;
    font-size: 1.5em;
    font-weight: 600;
    line-height: 2rem;
  }
  
  .card-subtitle {
    color: #859ba8;
    font-size: 0.8em;
  }
  
  /*Hover*/
  .card:hover {
    box-shadow: 0 8px 50px #23232333;
  }
  
  .card:hover .card-info {
    transform: translateY(-5%);
  }
  
  .card:hover .card-social {
    transform: translateY(100%);
    opacity: 1;
  }
  
  .card-social__item svg:hover {
    fill: #232323;
    transform: scale(1.1);
  }
  
  .card-avatar:hover {
    transform: scale(1.1);
  }
  
  .h1 {
    font-family: 'A';
  }
  
  
