.namex{
    font-family: 'Mali';
    font-size: 4rem;
  margin : auto;
}

.name2{
    font-family: 'Mali';
    font-size: 2rem;
  margin : 2%;
}
.pick{
    font-family: 'Mali';
}
.bkg{
    /* Background part */
    
    position: absolute; 
    width: 100%;
    height: 100%;
    background-image: url("../images/bicycle-doodle-set-2D9Y49D.png");    background-repeat: no-repeat;
    background-size: cover; 
    justify-content: center;
    /* background-size: 100% 100%; */
    background-repeat: no-repeat;
    overflow: auto;
    opacity: 1;
    z-index: -1;
    backface-visibility: visible;
    font-family: 'Mali';
    scroll-behavior: smooth;
    position: absolute;


}
.img1 {
    position: absolute;
    top: 4%;
    right: 5%;

    width: 9%;

    transition: transform .2s;
    /* Animation */
    transform: rotate(45deg);
    transition-duration: 0.8s;

}

.img1:hover {
    transform: scale(1.2);


}
.pc{
    font-family: 'Mali';
    font-size: 2rem;
  margin : 2%;
  text-decoration: none;
  color: green;

}
.pc:hover{
    opacity: 0.9;
}

