  .bgx {
    background: url("../images/images-bg.jpg") repeat-y center center/cover fixed;
  
    border-radius: 30px;
    box-shadow: 0 10px 50px rgba(22, 73, 41, 0.4);
    display: flex;
    flex-direction: row;
    width: 98%;
    margin: 1%;
    
    padding: 16px;
   
  }
  .dombox1 {
    /* Box part */
    background-color: #54B56A;
   
    position: absolute;
    left: 34%;
    padding: 10px;
    top: 30%;
    border-radius: 10px;
    font-family: 'Mali';

    color: #fff;

mix-blend-mode: normal;
border: 4px solid #CBE9FF;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
border-radius: 30px;
width: 35%;
height: 110%;
}

.link{
    /* already signup */
    font-family: 'Mali';
    font-style: bold;
    font-weight: 700;
    font-size: 1.52rem;
    line-height: 120%;
    display: flex;
    justify-content: center;
    text-align: center; 
    color: #FFFFFF;
    width: 100%;
    margin-top:6%;
    text-decoration: none;
    
    
}

.dombox1 .inputBox label {
/* email,name,password -title */
font-family: 'Mali';
font-style: normal;
font-weight: 700;
font-size: 1.875rem;
line-height: 111%;
display: flex;
align-items: center;
text-align: center;
letter-spacing: -0.04em;

color: #FFFFFF;


}


.dombox1 .inputBox input {
    /* inputpart for email-password-name */
    background: transparent;
    font-family: 'Mali';
    font-size: 1.35rem;
    
    border: 5px #606f69;
    width: 100%;
    padding: 10px;
    
    margin: 5px 0 22px 0;
    display: inline-block;
background: transparent;
mix-blend-mode: normal;
border: 4px solid #FFFFFF;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
border-radius: 10px;
    text-decoration: none;
}


.dombox1 input[type="submit"], .dombox1 button[type="submit"], a.button {
    /* signup button */
font-family: 'Mali';
margin: auto;
padding: 2.7%;
    

font-weight: 700;
font-size: 1.5rem;
align-items: center;
text-align: center;
display: flex;
color: #FFFFFF;
background: #54B56A;
mix-blend-mode: normal;
border: 4px solid #fff;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
border-radius: 30px;
width:40%;
justify-content: center;
text-decoration: none;



    
}

.dombox1 input[type="submit"]:hover, .dombox1 button[type="submit"]:hover, a.button:hover {
    opacity: 0.8;


}