

.sign1{
    /* Background part */
    
    position: absolute; 
    width: 100%;
    height: 100%;
    background-image: url("../images/bicycle-doodle-set-2D9Y49D.png");
    background-repeat: no-repeat;
    background-size: cover; 
    justify-content: center;


}
.img6r {
    position: absolute;
    bottom:1%;
    right: 2%;
   
    width: 20%;

  }
  .img7r {
    position: absolute;
    top:4%;
