    width: 10%;

  }
.h3{
    font-family: 'Mali';
}

.pick{
    /* Signup title */
    font-family: 'Mali';
    font-style: normal;
    font-weight: 700;
    font-size: 2.5rem;
    color: #FFFFFF;
    margin-top: 6%;
    margin-bottom: 14%;
} 

.box3 {
    /* Box part */
    background-color: #54B56A;
   
    position: absolute;
    left: 35%;
    padding: 10px;
    top: 9%;
    border-radius: 10px;
    font-family: 'Mali';

    color: #fff;

mix-blend-mode: normal;
border: 4px solid #CBE9FF;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
border-radius: 30px;
width: 36%;
height: 84%;
}

.link{
    /* already signup */
    font-family: 'Mali';
    font-style: italic;
    font-weight: 700;
    font-size: 1.52rem;
    line-height: 120%;
    display: flex;
    justify-content: center;
    text-align: center; 
    color: #FFFFFF;
    width: 100%;
    margin-top:3%;
    text-decoration: none;
    
    
}

.box3 .inputBox label {
/* email,name,password -title */
font-family: 'Mali';
font-style: normal;
font-weight: 700;
font-size: 1.875rem;
line-height: 111%;
display: flex;
align-items: center;
text-align: center;
letter-spacing: -0.04em;

color: #FFFFFF;


}


.box3 .inputBox input {
    /* inputpart for email-password-name */
    background: transparent;
    font-family: 'Mali';
    font-size: 1.35rem;
    
    border: 5px #606f69;
    width: 100%;
    padding: 10px;
    
    margin: 5px 0 22px 0;
    display: inline-block;
background: transparent;
mix-blend-mode: normal;
border: 4px solid #FFFFFF;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
border-radius: 10px;
    
}


.box3 input[type="submit"], .box3 button[type="submit"], a.button {
    /* signup button */
font-family: 'Mali';
margin: auto;
padding: 2.7%;
    

font-weight: 700;
font-size: 1.5rem;
align-items: center;
text-align: center;
display: flex;
color: #FFFFFF;
background: #54B56A;
mix-blend-mode: normal;
border: 4px solid #fff;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
border-radius: 30px;
width:40%;
justify-content: center;
text-decoration: none;


    
}

.box3 input[type="submit"]:hover, .box3 button[type="submit"]:hover, a.button:hover {
    opacity: 0.8;


}

.google-login-button {
    display: flex;
    justify-content: center; /* Center horizontally */
    align-items: center; /* Center vertically */
    text-align: center;
    background-color: #54B56A;
    color: white;
    border: none;
    /* margin:25%; */
    padding-left: 25%;
    padding-top: 5%;
    /* border-radius: 4px; */
    cursor: pointer;
    transition: background-color 0.3s;
    font-weight: bold;
    font-size: 16px;
    text-transform: uppercase;
  }
  
