.sign1 {
    /* Background part */

    position: absolute; 
    width: 100%;
    height: 100%;
    background-image: url("../images/bicycle-doodle-set-2D9Y49D.png");
    background-repeat: no-repeat;
    background-size: cover; 
    justify-content: center;


}

.img6r {
    position: absolute;
    bottom: 1%;
    right: 2%;

    width: 20%;

}

.img7r {
    position: absolute;
    top: 4%;
    left: 5%;

    width: 10%;

}

.h3 {
    font-family: 'Mali';
}

.pick2 {
    /* Signup title */
    font-family: 'Mali';
    font-style: normal;
    font-weight: 700;
    font-size: 4rem;
    color: #FFFFFF;
    margin-top: 15%;
    margin-bottom: 14%;
    margin-left: 10%;
    justify-content: center;
    
}

.box4 {
    /* Box part */
    background-color: #54B56A;

    position: absolute;
    left: 35%;
    padding: 10px;
    top: 20%;
    border-radius: 10px;
    font-family: 'Mali';

    color: #fff;

    mix-blend-mode: normal;
    border: 4px solid #CBE9FF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 30px;
    width: 36%;
    height: 50%;
    justify-content: center;
}

.link {
    /* already signup */
    font-family: 'Mali';
    font-style: italic;
    font-weight: 700;
    font-size: 1.52rem;
    line-height: 120%;
    display: flex;
    justify-content: center;
    text-align: center;
    color: #FFFFFF;
    width: 100%;
    margin-top: 6%;
    text-decoration: none;


}

.box4 .inputBox label {
    /* email,name,password -title */
    font-family: 'Mali';
    font-style: normal;
    font-weight: 700;
    font-size: 1.875rem;
    line-height: 111%;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: -0.04em;

    color: #FFFFFF;


}


.box4 .inputBox input {
    /* inputpart for email-password-name */
    background: transparent;
    font-family: 'Mali';
    font-size: 1.35rem;

    border: 5px #606f69;
    width: 100%;
    padding: 10px;

    margin: 5px 0 22px 0;
    display: inline-block;
    background: transparent;
    mix-blend-mode: normal;
    border: 4px solid #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;

}


.box4 input[type="submit"],
.box4 button[type="submit"],
a.button {
    /* signup button */
    font-family: 'Mali';
    margin: auto;
    padding: 2.7%;


    font-weight: 700;
    font-size: 1.5rem;
    align-items: center;
    text-align: center;
    display: flex;
    color: #FFFFFF;
    background: #54B56A;
    mix-blend-mode: normal;
    border: 4px solid #fff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 30px;
    width: 40%;
    justify-content: center;
    text-decoration: none;



}

.box4 input[type="submit"]:hover,
.box2 button[type="submit"]:hover,
a.button:hover {
    opacity: 0.8;


}

@media only screen and (max-width:500px) {}